import React, { useState, useEffect } from 'react';
import { FaTrash } from 'react-icons/fa';
import PropTypes from 'prop-types';

const TodoItem = (props) => {
  const {
    todo,
    handleChangeProps,
    deleteTodoProps,
    setUpdate,
  } = props;

  const [state, setState] = useState({
    editing: false,
  });

  const completedStyle = {
    fontStyle: 'italic',
    color: '#595959',
    opacity: 0.4,
    textDecoration: 'line-through',
  };

  const handleEditing = () => {
    setState({
      editing: true,
    });
  };
  const handleUpdatedDone = (e) => {
    if (e.key === 'Enter') {
      setState({ editing: false });
    }
  };

  useEffect(() => {
    // eslint-disable-next-line no-console
    console.log('Mounted', state.editing);
  }, []);

  return (
    <li className="item">
      <div onDoubleClick={handleEditing} style={state.editing ? { display: 'none' } : { display: 'block' }}>
        <input
          type="checkbox"
          checked={todo.completed}
          onChange={() => handleChangeProps(todo.id)}
        />
        <span style={todo.completed ? completedStyle : null}>
          {todo.title}
        </span>
        <button type="button" onClick={() => deleteTodoProps(todo.id)}>
          <FaTrash style={{ color: 'orangered', fontSize: '12px' }} />
        </button>
      </div>
      <input
        style={state.editing ? { display: 'block' } : { display: 'none' }}
        type="text"
        className="text-input"
        value={todo.title}
        onChange={(e) => {
          setUpdate(e.target.value, todo.id);
        }}
        onKeyDown={handleUpdatedDone}
      />
    </li>
  );
};

TodoItem.defaultProps = {
  todo: { id: 0, title: '', completed: false },
};

TodoItem.propTypes = {
  todo: PropTypes.shape({
    id: PropTypes.string,
    title: PropTypes.string,
    completed: PropTypes.bool,
  }),
  handleChangeProps: PropTypes.func.isRequired,
  deleteTodoProps: PropTypes.func.isRequired,
  setUpdate: PropTypes.func.isRequired,
};

export default TodoItem;
